<style scoped>
.video {
  margin-top: 20px;
}
.biliVideo {
  width: 1045px;
  height: 588px;
}
h2 {
  font-size: 34px;
  color: #333;
  margin-bottom: 10px;
}
.desc {
  font-size: 18px;
  color: #666;
  margin-bottom: 10px;
}
.author {
  font-size: 16px;
  color: #999;
  margin-bottom: 20px;
}
</style>

<template>
  <div class="w1160 video">
    <template v-if="videoInfo">
      <h2>{{videoInfo.title}}</h2>
      <p class="desc">{{videoInfo.desc}}</p>
      <p
        class="author"
      >作者：{{videoInfo.author}} up猪：{{videoInfo.uploader.nickname}} {{videoInfo.createTime}}</p>
      <div class="biliVideo" v-html="videoInfo.biliurl"></div>
    </template>
  </div>
</template>
<script>
export default {
  name: "videoDetail",
  data() {
    return {
      videoInfo: null
    };
  },
  created() {
    this.getVideoInfo();
  },
  methods: {
    async getVideoInfo() {
      let { data } = await axios.get("/getVideoInfo", {
        params: {
          pid: this.$route.params.pid
        }
      });
      if (data.status == 0) {
        this.videoInfo = data.data;
      } else {
        this.$message.error(data.message);
      }
    }
  }
};
</script>

